import React, { useEffect, useState } from "react";
import "./Banner.css";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import {motion} from 'framer-motion'
function Banner({ userName }) {
  console.log(userName);
  const imageUrl = "https://image.tmdb.org/t/p/original";
  const nav = useNavigate();
  let allMovies=JSON.parse(localStorage.getItem("all"));
  console.log(allMovies);

 
  let movie = allMovies[Math.floor(Math.random() * allMovies.length)];
  const bannerMovie = () => {
    let movieArray=[]
    movieArray.push(movie)
    localStorage.setItem("search", JSON.stringify(movieArray));
    nav("/over_view", {
      state: { name: userName.state.name },
    });
    // setTimeout(() => {
    //   window.location.reload();
    // }, 500);
  };

  return (
    <motion.div initial={{opacity:0}} animate={{opacity:1}} transition={{duration:1}}
      className="banner"
      style={{
        backgroundImage: `url(${movie ? imageUrl + movie.backdrop_path : ""})`,
      }}
    >
      <div className="fade_bottom-login"></div>
      <div className="content">
        <motion.h1 initial={{opacity:0,y:120}} animate={{opacity:1, y:0}} transition={{delay:1,duration:.8,type:'spring'}} className="title">{movie ? movie.title || movie.name : null}</motion.h1>
        <motion.div initial={{opacity:0,y:120}} animate={{opacity:1, y:0}} transition={{delay:1.5,duration:.8,type:'spring'}} className="banner_buttons">
          <button onClick={bannerMovie} className="button">
            View Details
          </button>
          <button className="button">My list</button>
        </motion.div>
        <motion.h1 initial={{opacity:0,y:120}} animate={{opacity:1, y:0}} transition={{delay:2,duration:.8,type:'spring'}} className="description">{movie ? movie.overview : null}</motion.h1>
      </div>
      <div className="fade_bottom"></div>
    </motion.div>
  );
}

export default Banner;
